<template>
  <div class="container">
    <h1 id="title">These are my Blogs</h1>
    <input id="search" class="form-control" type="text" v-model="search" placeholder="search blogs" />
    <div class="row">
      <div v-for="blog in filteredBlogs" :key="blog.id" class="col-lg-3 col-md-4 col-6 mb-5 d-flex justify-content-center">
        <div class="card">
          <img src="https://picsum.photos/600/300/?image=25" class="card-img-top">
          <div class="card-body">
            <h5 class="card-title" v-text="blog.title"></h5>
            <p class="card-text">{{ blog.content }}</p>
            <p class="card-text">{{ blog.author }}</p>
            <p class="card-text">
              HashTags:
              <ul>
                <li v-for="hashtag in blog.catagories" :key="hashtag.id"> # {{ hashtag }}</li>
              </ul>
            </p>
            <a href="#" class="btn btn-primary">See More</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import searchMixin from '../mixins/searchMixin';
import { db } from "@/config/firebaseConfig.js";
import { collection, getDocs } from "firebase/firestore";
export default {
  data() {
    return {
      blogs: [],
      search: '',
    };
  },
  mounted() {
    this.getBlogs();
  },
  methods: {
    async getBlogs() {
      const querySnapshot = await getDocs(collection(db, "blogs"));
      const blogs = [];
      querySnapshot.forEach((doc) => {
        let appData = doc.data();
        appData.id = doc.id;
        blogs.push({
          id: appData.id,
          title: appData.title,
          content: appData.content,
          author: appData.author,
          catagories: appData.catagories,
        });
      });
      this.blogs = blogs;
      // console.log(this.blogs);
    },
  },
  mixins: [searchMixin]
};
</script>

<style scoped>
#title{
  text-align: center;
  margin: 10px;
}
#search{
  width: 80vw;
  margin-bottom: 50px;
}
</style>