import homePage from './components/HomePage.vue';
import blogPost from './components/blogPost.vue';
import LoginVue from "./components/auth/LoginVue.vue";
import LogoutVue from "./components/auth/LogoutVue.vue";
import addBlog from "./components/addBlog.vue";
import RegisterVue from "./components/auth/RegisterVue.vue";
import ForgotPassword from './components/auth/ForgotPassword.vue';
import AdminPanel from './components/auth/AdminPanel.vue';
import { createRouter, createWebHistory } from "vue-router"
    const routeInfos = [
        {
            path : "/",
            component : homePage
        },
        {
            path : "/blogs",
            component : blogPost
        },
        { 
            path: "/addBlog",
            component: addBlog 
        },
        {
            path: "/login",
            component: LoginVue,
        },
        {
            path: "/logout",
            component: LogoutVue,
        },
        {
            path: "/register",
            component: RegisterVue,
        },
        {
            path: "/admin",
            component: AdminPanel,
        },
        {
            path: "/forgot-password",
            component: ForgotPassword,
        },
    ]
    const router = createRouter({
        history : createWebHistory(),
        routes : routeInfos
    })
    export default router;
